<template>
  <div :class="$style.app">
    <div :class="$style.overlay">
      <vue-particles
        :particleSize="30"
        :particleOpacity="0.04"
        shapeType="polygon"
        :linesWidth="4"
        :lineOpacity="0.15"
        color="#ffffff"
        linesColor="#eae0b3"/>
    </div>
    <div :class="$style.content">
      <h1 :class="$style.title">
        {{ fullName }}
        <span @click="() => bitten = !bitten">
          <font-awesome-icon v-if="bitten" icon="cookie-bite" />
          <font-awesome-icon v-else icon="cookie" />
        </span>
      </h1>
      <div :class="$style.nav">
        <router-link
          to="/"
          :exact-active-class="$style.active">
          Home
        </router-link> |
        <router-link
          to="/about"
          :exact-active-class="$style.active">
          CV
        </router-link>
      </div>
      <transition
        mode="out-in"
        :enter-class="$style.op0"
        :enter-active-class="$style.anim"
        :enter-to-class="$style.op1"
        :leave-class="$style.op1"
        :leave-active-class="$style.anim"
        :leave-to-class="$style.op0">
        <router-view></router-view>
      </transition>
    </div>
  </div>
</template>

<script>
import { setTimeout } from 'timers';
import t from '@/utils/messages';

export default {
  name: 'App',
  data() {
    return {
      bitten: false,
    };
  },
  computed: {
    fullName: () => `${t('PERSONAL', 'FIRST_NAME')} 
      ${t('PERSONAL', 'LAST_NAME')} 
      ${t('PERSONAL', 'FAMILY_NAME')} 
      ${t('PERSONAL', 'OTHER_NAME')} `,
  },
  methods: {
    t,
    scheduleBiteChange() {
      setTimeout(
        () => {
          this.bitten = !this.bitten;
          this.scheduleBiteChange();
        },
        500 + Math.random() * 1500,
      );
    },
  },
  mounted() {
    this.scheduleBiteChange();
  },
};
</script>

