@import url('https://fonts.googleapis.com/css?family=Open+Sans');

body {
  background-color: #006bb2;
  margin: 0,;
  padding: 0;
}

.app {
  font-family: 'Open Sans', sans-serif;
  text-align: center;
  color: #fff;
  text-shadow: 0 0.05rem 0.1rem rgba(0, 0, 0, .5);
}

.content {
  padding: 60px 30px;
}

.nav {
  display: none;
  padding: 30px;

  a {
    font-weight: bold;
    color: rgba(255, 255, 255, .5);
  }
}

.active {
  color: #f4ca16 !important;
}

.anim {
  transition: all 0.3s ease;
}

.op1 {
  opacity: 1;
}

.op0 {
  opacity: 0;
}

.title {
  font-size: 2.5rem;
  text-transform: capitalize;
  margin: 0;
}

.overlay {
  position: fixed;
  height: 100%;
  width: 100%;
  box-shadow: inset 0 0 5rem rgba(0, 0, 50, 1);
  pointer-events: none;
}
</style>
