<template>
  <div
    :class="$style.hoverShine"
    @click="showHide">
    <div :class="$style.title">
      <div :class="$style.first">
        <span><b>{{ role }}</b></span>
        <span>{{ company }}</span>
      </div>
      <div :class="$style.second">
        <span>
          {{ moment(startDate).format("MM/YYYY") }}
          - {{ finishDate ? moment(finishDate).format("MM/YYYY") : 'Actualidad' }}
        </span>
        <i>
          ({{ finishDate
            ? moment(finishDate).diff(startDate, 'months')
            : moment().diff(startDate, 'months') }} meses)
        </i>
      </div>
    </div>
    <transition
      mode="out-in">
      <div
        v-if="showSummary"
        :class="$style.summary">
        {{ summary }}
      </div>
    </transition>
    <div :class="$style.keywords">
      <span
        v-for="m in keywords"
        :key="m"
        :class="$style.tag">
        {{ m }}
      </span>
    </div>
  </div>
</template>

<script>
import moment from 'moment';

export default {
  name: 'ProfessionalJob',
  props: {
    role: String,
    company: String,
    startDate: String,
    finishDate: String,
    summary: String,
    keywords: Array,
  },
  data() {
    return {
      showSummary: false,
    };
  },
  methods: {
    moment,
    showHide() {
      this.showSummary = !this.showSummary;
    },
  },
};
</script>

